import React, { useState } from 'react';
import './Expenses.css';
import ExpenseItem from './ExpenseItem';
import Card from '../UI/Card';
import ExpensesFilter from './ExpenseFilter';

function Expenses(props: { date: Date; title: string; amount: number }[]) {
  const [filteredYear, setFilteredYear] = useState('2020');
  const filteredChangeHandler = (selectedYear: string) => {
    setFilteredYear(selectedYear);
  };

  return (
    <Card className="expenses">
      <ExpensesFilter selected={filteredYear} onChangeFilter={filteredChangeHandler} />
      {props.map(prop => {
        return <ExpenseItem {...prop} />;
      })}
    </Card>
  );
}

export default Expenses;
